import "./globals.css";

import type { Metadata } from "next";
import { Inter } from "next/font/google";
import Image from "next/image";
import Link from "next/link";
import Social from "../components/Social";

const inter = Inter({ subsets: ["latin"] });

export const metadata: Metadata = {
  title: "Didier H.",
  description:
    "Didier H. | Personal portfolio, Web developer based in Sydney, Australia üíªüåê",
  applicationName: "Didier H. | Personal portfolio",
  keywords: [
    "software engineer",
    "web developer",
    "portfolio",
    "sydney",
    "australia",
    "react",
    "nextjs",
    "typescript",
    "javascript",
    "nodejs",
    "graphql",
    "apollo",
  ],
  authors: [{ name: "Didier Hategekimana" }],
  robots: "index,follow",
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en">
      <body className={inter.className} suppressHydrationWarning={true}>
        <main className="container">
          <div className="header-section">
            <div className="header-frame">
              <h1>Didier H.</h1>
              <h2>Software Engineer in Sydney</h2>
            </div>
            <div className="gradient-box">
              <Image
                src="/avatar.jpg"
                className="img-frame"
                alt="Didier H"
                width={200}
                height={200}
              />
            </div>
          </div>
          <section className="tabs-section">
            <div className="tabs">
              <Link href="/" className="tab">
                Short
              </Link>
              <Link href="/about" className="tab">
                About
              </Link>
              <Link href="/contact" className="tab">
                Contact
              </Link>
            </div>
            <div className="tab-content">{children}</div>
          </section>
        </main>
        <footer>
          <Social />
        </footer>
      </body>
    </html>
  );
}
